import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import GlobalSettings from '@/GlobalSettings'
import store from '../store'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    // form: false,
  },
  image: {
    type: IKDataEntity.Types.Image,
    formConfig: {
      type: {
        root: () => store.getters.rootUrl + GlobalSettings.bannerImageRoot,
      },
      required: false,
    },
    displayName: 'image',
  },
  sort: {
    displayName: 'Ordnung',
    type: IKDataEntity.Types.Integer,
    formConfig: {
      // required: true,
    },
  },
  remark: {
    type: IKDataEntity.Types.String,
    displayName: 'remark',
    formConfig: {
      required: false,
    },
  },
}
const config = {
  add: function (item) {
    return hillo.postWithUploadFile('Restaurant.php?op=addGiraffeBannerImage', {
      ...item,
    }, { showLoading: true })
  },
  edit: function (item) {
    // if (!item.bannerImage) { item.bannerImage = item.bannerImage.split('/')[1] }

    return hillo.postWithUploadFile('Restaurant.php?op=modifyRestaurantImage', {
      ...item,
      params: JSON.stringify(item),
    }, { showLoading: true })
  },
  remove: function (id) {
    return hillo.post('Restaurant.php?op=deleteGiraffeBannerImage', { id: id })
  },
  load: async function () {
    return (await hillo.get('Restaurant.php?op=showGiraffeBannerImage', {}))
      .content
  },
}

export default IKDataEntity.ModelFactory(entity, config)
